@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    --bg: #0b0e13;
    --bg-soft: #0f131a;
    --text: #e6f0ff;
    --muted: #a9b1c6;
    --brand: #6c9cff;
    --brand-2: #a66cff;
    --card: #121723;
    --border: #1d2433;
    --shadow: 0 10px 30px rgba(0,0,0,0.35);
}

:root[data-theme="light"] {
    --bg: #f7f9fc;
    --bg-soft: #ffffff;
    --text: #101319;
    --muted: #555e72;
    --brand: #365cff;
    --brand-2: #a33cff;
    --card: #ffffff;
    --border: #e8edf6;
}

* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body {
    margin: 0;
    font-family: Inter, system-ui, -apple-system, Segoe UI, Roboto, Arial, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
    background: radial-gradient(1200px 600px at 80% -10%, rgba(108,156,255,0.2), transparent 60%),
                radial-gradient(800px 400px at 20% -10%, rgba(166,108,255,0.15), transparent 60%), var(--bg);
    color: var(--text);
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

main { overflow: visible; }
a { color: inherit; text-decoration: none; }

.container {
    width: min(1100px, 92vw);
    margin: 0 auto;
}

/* --- SECTION BACKGROUND FIX --- */
.section {
  position: relative;
  padding: 100px 0;
  width: 100vw;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  z-index: 1;
  background: var(--bg); /* fallback, overwritten by section-gradient */
}

.section-gradient {
  background: linear-gradient(180deg, #101213 70%, #23263a 100%);
}

.section-title {
    font-size: clamp(28px, 4vw, 42px);
    letter-spacing: -0.02em;
    margin: 0 0 16px;
}

.section-subtitle {
    color: var(--muted);
    margin-bottom: 32px;
}

.btn {
    display: inline-flex;
    align-items: center;
    gap: 10px;
    padding: 12px 18px;
    border-radius: 12px;
    background: linear-gradient(135deg, var(--brand), var(--brand-2));
    color: white;
    border: none;
    cursor: pointer;
    box-shadow: var(--shadow);
    transition: transform .2s ease, box-shadow .2s ease, filter .2s ease;
}
.btn:hover { transform: translateY(-2px); filter: brightness(1.05); }

.btn-outline {
    background: transparent;
    border: 1px solid var(--border);
    color: var(--text);
}

.card {
    background: linear-gradient(180deg, var(--card), var(--bg-soft));
    border: 1px solid var(--border);
    border-radius: 16px;
    box-shadow: var(--shadow);
}

.grid { display: grid; gap: 18px; }
.grid-3 { grid-template-columns: repeat(1, 1fr); }
@media (min-width: 840px) {
    .grid-3 { grid-template-columns: repeat(3, 1fr); }
}

/* Navbar */
.navbar {
    position: sticky;
    top: 0;
    z-index: 50;
    backdrop-filter: saturate(120%) blur(10px);
    background: color-mix(in oklab, var(--bg) 75%, transparent);
    border-bottom: 1px solid var(--border);
}
.navbar-inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 64px;
}
.nav-links {
    display: flex;
    gap: 18px;
    align-items: center;
}
.nav-link {
    color: var(--muted);
    font-weight: 500;
}
.nav-link:hover { color: var(--text); }

/* Hero */
.hero {
    padding: 120px 0 96px;
    position: relative;
}
.title-xl {
    font-size: clamp(40px, 8vw, 72px);
    margin: 0 0 12px;
    letter-spacing: -0.03em;
}
.title-grad {
    background: linear-gradient(135deg, var(--brand) 0%, var(--brand-2) 100%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}
.hero-lead {
    color: var(--muted);
    max-width: 740px;
    font-size: clamp(16px, 2.4vw, 20px);
}
.hero-cta { display: flex; gap: 12px; margin-top: 28px; }

/* Project card */
.project-card {
    position: relative;
    overflow: clip;
    padding: 18px;
    display: flex;
    flex-direction: column;
    gap: 12px;
}
.project-thumb {
    aspect-ratio: 16 / 10;
    border-radius: 12px;
    background: linear-gradient(135deg, rgba(108,156,255,.15), rgba(166,108,255,.10));
    border: 1px solid var(--border);
}
.project-title {
    font-weight: 700;
    margin: 6px 0 2px;
}
.project-tags {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
    color: var(--muted);
    font-size: 13px;
}

/* Contact */
.contact-card { padding: 24px; }
.input, .textarea {
    width: 100%;
    padding: 12px 14px;
    border-radius: 12px;
    border: 1px solid var(--border);
    background: var(--bg-soft);
    color: var(--text);
}
.textarea { min-height: 120px; resize: vertical; }

/* Footer */
.footer {
    padding: 36px 0 60px;
    color: var(--muted);
    text-align: center;
    border-top: 1px solid var(--border);
}

/* Progress bar */
.progress {
    position: fixed;
    inset: 0 0 auto 0;
    height: 3px;
    background: linear-gradient(90deg, var(--brand), var(--brand-2));
    transform-origin: 0% 50%;
    z-index: 100;
}

/* Small Card grid for Education/Certifications */
.card-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: flex-start;
}
.card-sm {
    min-width: 220px;
    max-width: 300px;
    flex: 1 1 220px;
    background: var(--card);
    border: 1px solid var(--border);
    border-radius: 14px;
    box-shadow: var(--shadow);
    padding: 20px 18px 14px 18px;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    transition: transform 0.18s cubic-bezier(.62,1.5,.36,1), box-shadow 0.18s;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.card-sm:hover {
    transform: translateY(-5px) scale(1.03) rotate(-1deg);
    box-shadow: 0 8px 38px 0 rgba(108,156,255,0.13), 0 1.5px 7px 0 var(--brand-2);
    border-color: var(--brand-2);
}
.card-sm .card-title {
    font-weight: 700;
    font-size: 17px;
    margin-bottom: 3px;
}
.card-sm .card-sub {
    color: var(--brand);
    font-weight: 600;
    margin-bottom: 4px;
    font-size: 15px;
}
.card-sm .card-tag {
    color: var(--muted);
    font-size: 13px;
    margin-bottom: 7px;
}
.card-sm .card-desc {
    color: var(--text);
    font-size: 14px;
}
@media (max-width: 720px) {
    .card-list {
        flex-direction: column;
        gap: 14px;
    }
    .card-sm {
        max-width: none;
        width: 100%;
    }
}

.card-logo {
    display: block;
    margin: 0 auto 12px auto;
    width: auto;
    height: 52px;
    max-width: 90%;
    object-fit: contain;
    background: #fff;
    border-radius: 6px;
    box-shadow: 0 1px 6px 0 rgba(0,0,0,0.07);
}
.card-sm {
    min-width: 220px;
    max-width: 280px;
    flex: 1 1 220px;
    background: var(--card);
    border: 1px solid var(--border);
    border-radius: 14px;
    box-shadow: var(--shadow);
    padding: 18px 14px 14px 14px;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: transform 0.18s cubic-bezier(.62,1.5,.36,1), box-shadow 0.18s;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    min-height: 232px; /* Adjust to fit content and logo uniformly */
}
.card-title {
    font-weight: 700;
    font-size: 17px;
    margin-bottom: 3px;
    text-align: center;
}
.card-sub {
    color: var(--brand);
    font-weight: 600;
    margin-bottom: 4px;
    font-size: 15px;
    text-align: center;
}
.card-tag {
    color: var(--muted);
    font-size: 13px;
    margin-bottom: 7px;
    text-align: center;
}
.card-desc {
    color: var(--text);
    font-size: 14px;
    text-align: center;
}
@media (max-width: 720px) {
    .card-list {
        flex-direction: column;
        gap: 14px;
    }
    .card-sm {
        max-width: none;
        width: 100%;
    }
}